import { createRouter, createWebHistory } from 'vue-router';
import CharacterPage from '../components/pages/CharacterPage.vue';
import ActivityPage from '../components/pages/ActivityPage.vue';
import WorkPage from '../components/pages/WorkPage.vue';
import PropertiesPage from '../components/pages/PropertiesPage.vue';

import FoodPage from '../components/pages/activity_pages/FoodPage.vue';
import ClothesPage from '../components/pages/activity_pages/ClothesPage.vue';

const routes = [
  { path: '/', name: 'CharacterPage', component: CharacterPage },
  { 
    path: '/activity', 
    name: 'ActivityPage', 
    component: ActivityPage,
  },
  { path: '/activity/food', name: 'FoodPage', component: FoodPage },
  { path: '/activity/clothes', name: 'ClothesPage', component: ClothesPage },
  { path: '/work', name: 'WorkPage', component: WorkPage },
  { path: '/properties', name: 'PropertiesPage', component: PropertiesPage},
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
});

export default router;
