<!-- CustomButton.vue -->
<template>
  <button 
    :class="['basic-button', { 'no-color': backgroundColor === '' }]"
    :style="{ backgroundColor: computedBackgroundColor }"
  >
    <slot></slot>
  </button>
</template>

<script>
export default {
  name: 'CustomButton',
  props: {
    backgroundColor: {
      type: String,
      default: '' // Якщо не передано параметр, він залишається порожнім
    }
  },
  computed: {
    computedBackgroundColor() {
      // Мапінг кольорів за назвами
      const colorMap = {
        green: '#4caf50', // Стильний зелений (Material Design)
        red: '#e57373', // Стильний червоний (Material Design)
        blue: '#64b5f6', // Стильний блакитний (Material Design)
        purple: '#9575cd', // Стильний фіолетовий
        orange: '#ffb74d' // Стильний помаранчевий
      };

      // Якщо передано значення, перевіряємо в мапі кольорів
      // Якщо знайдено в мапі, використовуємо відповідний колір
      return colorMap[this.backgroundColor] || ''; // Якщо кольору немає в мапі, залишаємо стандартний фон
    }
  },
  methods: {
    handleClick() {
      this.$emit('click'); // Викликає подію кліку
    }
  }
}
</script>

<style scoped>
.basic-button {
  width: 160px;
  height: 50px;
  color: white;
  font-size: 18px;
  font-weight: bold;
  background-size: cover;
}
.no-color {
  background-image: url("../../assets/img/depositphotos_9932911-stock-photo-fabric-textile-textured-background.jpg");
}
</style>
