<template>
  <div class="activities">
    <div class="slider-container" v-if="horizontal">
      <CustomButton class="arrow left-arrow" @click="prevActivity">&lt;</CustomButton>
      <div class="slider">
        <div 
          v-for="(activity, index) in activities" 
          :key="index" 
          class="activity main-block slide"
          :style="{ transform: `translateX(-${currentActivityIndex * 106}%)` }"
        >
          <router-link v-if="activity.url" :to="`/activity/${activity.url}`"></router-link>
          <img :src="activity.imgSrc" :alt="activity.title" />
          <div>
            <h3>{{ activity.name }}</h3>
            <div v-if="!activity.url" class="activity-params">
              <div v-for="(param, paramKey) in activity.params" :key="paramKey">
                <img :src="getParamIcon(paramKey)" :alt="paramKey" />
                <div>
                  <span v-if="param.value > 0">+</span>{{ param.value }}
                  <span v-if="param.everyDay"> кожного дня</span>
                </div>
              </div>
            </div>
            <CustomButton 
              class="buy-sell-button" 
              @click="sellOrBuy(activity)" 
              :backgroundColor="isActivityInList(activity) ? 'red' : 'green'"
            >
              {{ isActivityInList(activity) ? `Продати: ${activity.soldCost}` : `Купити: ${activity.cost}` }}
            </CustomButton>
          </div>
        </div>
      </div>
      <CustomButton class="arrow right-arrow" @click="nextActivity">&gt;</CustomButton>
    </div>

    <div v-else>
      <div v-for="(activity, index) in activities" :key="index" class="activity main-block">
        <router-link v-if="activity.url" :to="`/activity/${activity.url}`"></router-link>
        <img :src="activity.imgSrc" :alt="activity.title" />
        <div>
          <h3>{{ activity.title }}</h3>
          <div v-if="!activity.url" class="activity-params">
            <div v-for="(param, paramKey) in activity.params" :key="paramKey">
              <img :src="getParamIcon(paramKey)" :alt="paramKey" />
              <div>
                <span v-if="param.value > 0">+</span>{{ param.value }}
                <span v-if="param.everyDay"> кожного дня</span>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import CustomButton from '@/components/basic_components/CustomButton.vue';
import { mapState, mapActions } from 'vuex';

import heartIcon from '@/assets/img/heart (1).png';
import wreathIcon from '@/assets/img/wreath.png';
import coinIcon from '@/assets/img/coin.png';

export default {
  name: 'ActivityList',
  components: {
    CustomButton,
  },
  props: {
    activities: {
      type: Array,
      required: true,
      default: () => []
    },
    horizontal: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      currentActivityIndex: 0
    };
  },
  computed: {
    ...mapState(['activitiesList'])
  },
  methods: {
    ...mapActions(['addActivity', 'removeActivity', 'updateMoney', 'updateHealth', 'updateReputation']),
    nextActivity() {
      if (this.currentActivityIndex < this.activities.length - 1) {
        this.currentActivityIndex++;
      }
    },
    prevActivity() {
      if (this.currentActivityIndex > 0) {
        this.currentActivityIndex--;
      }
    },
    getParamIcon(paramKey) {
      switch (paramKey) {
        case 'health':
          return heartIcon;
        case 'reputation':
          return wreathIcon;
        case 'money':
          return coinIcon;
        default:
          return ''; // Повертаємо порожнє значення, якщо іконки немає
      }
    },
    isActivityInList(activity) {
      return this.activitiesList.includes(activity.name);
    },
    sellOrBuy(activity) {
      const cost = activity.cost;
      const soldCost = activity.soldCost;
      const currentMoney = this.$store.state.money;

      if (this.isActivityInList(activity)) {
        // Продати активність, повертаємо частину вартості
        this.removeActivity(activity.name);
        this.updateMoney(soldCost); // Додаємо гроші за продаж
        
        // Змінюємо інші параметри, якщо вони не щоденні
        Object.keys(activity.params).forEach(paramKey => {
          const param = activity.params[paramKey];
          if (!param.everyDay) {
            switch (paramKey) {
              case 'health':
                this.updateHealth(-param.value); // Зменшуємо параметр при продажу
                break;
              case 'reputation':
                this.updateReputation(-param.value);
                break;
              case 'money':
                this.updateMoney(-param.value); // Якщо параметр змінює гроші
                break;
              default:
                break;
            }
          }
        });
        
      } else {
        if (currentMoney >= cost) {
          // Купити активність, якщо вистачає грошей
          this.addActivity(activity.name);
          this.updateMoney(-cost); // Віднімаємо гроші за купівлю
          
           // Змінюємо інші параметри, якщо вони не щоденні
          Object.keys(activity.params).forEach(paramKey => {
            const param = activity.params[paramKey];
            if (!param.everyDay) {
              switch (paramKey) {
                case 'health':
                  this.updateHealth(param.value); // Збільшуємо параметр при купівлі
                  break;
                case 'reputation':
                  this.updateReputation(param.value);
                  break;
                case 'money':
                  this.updateMoney(param.value); // Якщо параметр змінює гроші
                  break;
                default:
                  break;
              }
            }
          });
        } else {
          alert('Недостатньо грошей для купівлі активності');
        }
      }
    }
  }
}
</script>

<style scoped>
.activity {
  display: flex;
  justify-content: space-between;
  align-items: center;    
  flex-direction: column;
  margin: 10px;

  position: relative;
}

.buy-sell-button {
  margin: 20px 0;
}

.activity a {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 10; 
}

.activity img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.activity-params img {
  width: 25px;
  height: 25px;
}

.activity-params > div {
  display: flex;
  justify-content: center;
  align-items: center;
}

.activity-params > div > div {
  margin: 0 10px;
}

/* Стили для слайдера */
.slider-container {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}

.slider {
  display: flex;
  overflow: hidden;
  width: 100%; /* Задайте ширину, якщо потрібно */
}

.slide {
  width: 100%;
}

.activity {
  min-width: 300px; /* Встановіть ширину для активностей */
  transition: transform 0.5s ease;
}

.left-arrow,
.right-arrow { /* Колір стрілок */
  color: white; /* Колір тексту стрілок */
  padding: 10px;

  max-width: 30px;
}

.left-arrow {
  border-radius: 5px 0 0 5px; /* Стилізація стрілки зліва */
}

.right-arrow {
  border-radius: 0 5px 5px 0; /* Стилізація стрілки справа */
}
</style>