<template>
    <div class="header">
      <div class="resources">
        <div class="resource">
          <div class="basic-block">
            <img src="../assets/img/heart (1).png" alt="Health" />
          </div>
          <div class="basic-block" alt="health">
            {{ health }}
          </div>
        </div>
        <div class="resource">
          <div class="basic-block">
            <img src="../assets/img/wreath.png" alt="Reputation" />
          </div>
          <div class="basic-block" alt="reputation">
            {{ reputation }}
          </div>
        </div>
        <div class="resource">
          <div class="basic-block">
            <img src="../assets/img/coin.png" alt="Money" />
          </div>
          <div class="basic-block" alt="money">
            {{ money }}
          </div>
        </div>
        <div class="resource clock" @click="applyDailyEffects">
          <div class="basic-block">
            <img src="../assets/img/sand-clock.png" alt="Clock" />
          </div>
          <div></div>
        </div>
      </div>
      <div class="tabs">
        <button class="tab basic-block">
          <router-link to="/">
            <img src="../assets/img/png-clipart-computer-icons-man-icon-logo-silhouette.png" alt="Tab 1" />
          </router-link>
        </button>
        <button class="tab basic-block">
          <router-link to="/activity">
            <img src="../assets/img/apple.png" alt="Tab 2" />
          </router-link>
        </button>
        <button class="tab basic-block">
          <router-link to="/properties">
            <img src="../assets/img/medieval-house.png" alt="Tab 3" />
          </router-link>
        </button>
        <button class="tab basic-block">
          <router-link to="/work">
            <img src="../assets/img/scythe.png" alt="Tab 4" />
          </router-link>
        </button>
      </div>
    </div>
  </template>
  
  <script>
  import { mapState, mapActions } from 'vuex';
  import { config } from '../GameConfig.js'; // імпортуємо конфіг
  
  export default {
    name: 'AppHeader',
    computed: {
      ...mapState(['health', 'reputation', 'money', 'activitiesList'])
    },
    methods: {
      ...mapActions(['updateHealth', 'updateReputation', 'updateMoney', 'incrementDays']),
      
      applyDailyEffects() {
        const activeItems = this.activitiesList || [];

        if (this.money === 0) {
          return;
        }

        config.items.forEach((item) => {
          if (activeItems.includes(item.name)) {
            Object.keys(item.params).forEach((param) => {
              if (item.params[param].everyDay) {
                const value = item.params[param].value;
                switch (param) {
                  case 'health':
                    this.updateHealth(value);
                    break;
                  case 'reputation':
                    this.updateReputation(value);
                    break;
                  case 'money':
                    this.updateMoney(value);
                    break;
                  default:
                    break;
                }
              }
            });
          }
    });

    this.incrementDays(); // Інкрементуємо дні
  }
    }
  };
  </script>
  
  <style scoped>
  .header {
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    padding: 20px;
    background-image: url('../assets/img/wooden.jpg');
  }
  
  .resources {
    display: flex;
    justify-content: space-between;
  }
  
  .resource {
    display: flex;
  }
  
  .resource.clock {
    cursor: pointer;
  }
  
  .resource .basic-block {
    min-width: 20px;
    min-height: 20px;
    max-width: 20px;
    max-height: 20px;
  }
  
  .resource .basic-block:last-child {
    max-width: 5000px;
    border-right: 1px solid #646363;
  }
  
  .tabs {
    display: flex;
    justify-content: center;
    margin-bottom: 20px;
    margin-top: 10px;
  }
  
  .tab {
    min-width: 53px;
    min-height: 53px;
    width: 53px;
    height: 53px;
    transition: transform 0.3s;
    padding: 0 !important;
  }
  
  .tab a {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0;
  }
  
  .tab .basic-block {
    padding: 0;
  }
  
  .tab:hover {
    background-color: rgba(253, 216, 160, 1);
  }
  
  .tab.active {
    background-color: rgba(253, 216, 160, 1);
  }
  
  .tab:last-child {
    border-right: 1px solid #646363;
  }
  
  .tab img,
  .resource img,
  .basic-block img {
    width: 70%;
    height: 70%;
    object-fit: cover;
  }
  
  .resource img {
    width: 100%;
    height: 100%;
  }
  </style>
  