import { createStore } from 'vuex';

export default createStore({
  state: {
    name: localStorage.getItem('name') || '',
    health: Number(localStorage.getItem('health')) || 100, // Перетворюємо в число
    reputation: Number(localStorage.getItem('reputation')) || 0, // Перетворюємо в число
    money: Number(localStorage.getItem('money')) || 100, // Перетворюємо в число
    activitiesList: JSON.parse(localStorage.getItem('activitiesList')) || [] // Ініціалізуємо як пустий масив, якщо значення null
  },
  mutations: {
    initializeState(state) {
      // Ініціалізуємо значення лише один раз
      if (!localStorage.getItem('name')) {
        localStorage.setItem('name', 'John Doe');
        localStorage.setItem('health', '100');
        localStorage.setItem('reputation', '0');
        localStorage.setItem('money', '100');
        localStorage.setItem('activitiesList', JSON.stringify([])); // Ініціалізуємо масив активностей
        localStorage.setItem('days', '0');
      }

      state.name = localStorage.getItem('name');
      state.health = Number(localStorage.getItem('health'));
      state.reputation = Number(localStorage.getItem('reputation'));
      state.money = Number(localStorage.getItem('money'));
      state.activitiesList = JSON.parse(localStorage.getItem('activitiesList')) || [];
      state.days = Number(localStorage.getItem('days'));
    },

    incrementDays(state) {
      state.days = Number(state.days) + 1;
      localStorage.setItem('days', state.days.toString());
    },
    setName(state, newName) {
      state.name = newName;
      localStorage.setItem('name', newName);
    },
    setMoney(state, newMoney) {
      state.money = Math.max(state.money + Number(newMoney), 0); // Перевірка на нуль
      localStorage.setItem('money', state.money.toString());
    },
    setHealth(state, newHealth) {
      state.health = Math.max(state.health + Number(newHealth), 0); // Перевірка на нуль
      localStorage.setItem('health', state.health.toString());
    },
    setReputation(state, newReputation) {
      state.reputation = Math.max(state.reputation + Number(newReputation), 0); // Перевірка на нуль
      localStorage.setItem('reputation', state.reputation.toString());
    },
    addActivity(state, activity) {
      state.activitiesList.push(activity);
      localStorage.setItem('activitiesList', JSON.stringify(state.activitiesList));
    },
    removeActivity(state, activity) {
      state.activitiesList = state.activitiesList.filter(item => item !== activity);
      localStorage.setItem('activitiesList', JSON.stringify(state.activitiesList));
    }
  },
  actions: {
    initializeState({ commit }) {
      commit('initializeState');
    },
    incrementDays({ commit }) {
      commit('incrementDays'); // Викликаємо інкремент днів
    },
    updateName({ commit }, newName) {
      commit('setName', newName);
    },
    updateHealth({ commit }, newHealth) {
      commit('setHealth', newHealth);
    },
    updateReputation({ commit }, newReputation) {
      commit('setReputation', newReputation);
    },
    updateMoney({ commit }, newMoney) {
      commit('setMoney', newMoney);
    },
    addActivity({ commit }, activity) {
      commit('addActivity', activity);
    },
    removeActivity({ commit }, activity) {
      commit('removeActivity', activity);
    }
  }
});
