<template>
  <div class="container">
    <Header/>
    <div class="pages">
      <router-view></router-view>
    </div>
    <div v-if="shouldShowBackButton" class="back-button">
      <CustomButton @click="returnBack">Назад</CustomButton>
    </div>
  </div>
</template>

<script>
import Header from './components/Header.vue'
import CustomButton from './components/basic_components/CustomButton.vue'

export default {
  name: 'App',
  components: {
    Header,
    CustomButton
  },
  computed: {
    shouldShowBackButton() {
      return this.$route.path.split('/').length > 2;
    }
  },
  methods: {
    returnBack() {
      const pathSegments = this.$route.path.split('/');
      if (pathSegments.length > 1) {
        pathSegments.pop(); // Видаляємо останній елемент
        const previousPath = pathSegments.join('/') || '/'; // Збираємо шлях назад, якщо масив порожній, то встановлюємо '/'
        this.$router.push(previousPath);
      }
    }
  }
}
</script>

