import { createApp } from 'vue';
import App from './App.vue';
import router from './router/index.js';
import store from '../store'; // Шлях до вашого файлу store.js

// Створюємо екземпляр додатку і підключаємо роутер та сховище
const app = createApp(App);
app.use(router); // Підключаємо роутер
app.use(store); // Підключаємо сховище Vuex

// Монтуємо додаток на елемент з ID #app
app.mount('#app');

// Ініціалізація стану при запуску додатку
store.dispatch('initializeState');